@import 'src/mixin.scss';

.video-list {
  display: flex;
  flex-wrap: wrap;

  .video-item {
    width: 33%;
    padding: 2%;
    margin-bottom: 1%;
    box-sizing: border-box;
    
    @include pc-1024 {
      width: 50%;
      padding: 2.5%;
      margin-bottom: 1%;
    }
    @include pad {
      width: 100%;
      padding: 3%;
      margin-bottom: 2%;
    }

    .label {
      height: 35px;
      display: flex;
      align-items: center;
      line-height: 14px;
    
      .label-icon {
        height: 35px;
      }

      .label-text {
        flex: 1;
        overflow: hidden;
        padding-left: 5px;
        padding-right: 5px;
      }

      .title {
        font-weight: bolder;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }

      .sub-title {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        overflow: hidden;

        .date {
          display: flex;
          align-items: center;
          white-space: nowrap;
        }

        .site-name {
          display: flex;
          align-items: center;
          white-space: nowrap;
          overflow: hidden;
          
          span {
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }

        img {
          height: 14px;
          padding-top: 1px;
        }
      }
    }

    .player-wrapper {
      margin-top: 5px;
      position: relative;
      padding-top: 56.25%;
      background-color: black;
      background-image: url('/assets/spacex_logo.png');
      background-size: 60% 60%;
      background-repeat: no-repeat;
      background-position: 65% 50%;
    }

    .react-player {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}